<template>
	<Tabs value="account" class="tab">
		<Tab-pane label="台账报表" name="account" class="tab-item">
			<search-box @search="search" :refreshFlag="refreshFlag" :itemList="searchItemList"></search-box>
			<div class="tableHeader">
				<Button class="export" type="text" icon="share" size="large" :disabled="disabled" @click="downLoadExcel">导出EXCEL</Button>
			</div>
			<Table highlight-row size="large" :columns="account.header" :data="account.list"></Table>
			<div class="page">
				<Page placement='top' :total="account.totalPage" :current="searchParams.currentPage" :page-size="searchParams.pageSize"
					  :page-size-opts="[10,20,30,40]"
					  show-elevator show-sizer show-total
					  @on-change="pageChange" @on-page-size-change="pageSizeChange"
				></Page>
			</div>
		</Tab-pane>
		<i-button type="text" slot="extra" class="extra-btn" :disable="refreshFlag" icon="ios-loop-strong" @click="refresh">刷新</i-button>
	</Tabs>
</template>
<script>
	import searchBox from '@/components/searchbox.vue'
	export default {
		data () {
			return {
				searchItemList:['dateRange','channelCode'],
				refreshFlag:false,
				searchParams: {
					startTime:'',
					endTime:'',
					loanVoucher:-1,
					currentPage: 1,
					pageSize: 10
				},
				account:{
					header:[
						{
							type: 'index',
							title: '序号',
							align: 'center'
						},
						{
							title: '放款渠道',
							key: 'channelCode',
							align: 'center',
							render:(h,params)=>{
								let code=params.row.channelCode;
								let channelName='';
								switch (code) {
									case 1802:
										channelName = '外贸信托';
										break;
									case 1804:
										channelName = '新网银行';
										break;
									default:
										channelName = '未知渠道';
								}
								return h('span',channelName)
							}
						},
						{
							title: '城市',
							key: 'city',
							align: 'center'
						},
						{
							title: '借款人姓名',
							key: 'name',
							align: 'center'
						},
						{
							title: '身份证号',
							key: 'idNumber',
							align: 'center'
						},
						{
							title: '贷款金额',
							key: 'appMoney',
							align: 'center'
						},
						{
							title: '我行认定估价',
							key: 'nbComfirmValue',
							align: 'center',
							render:(h,params)=>{
								return h('span',this.fmoney(params.row.nbComfirmValue))
							}
						},
						{
							title: '期限',
							key: 'periods',
							align: 'center'
						},
						{
							title: '还款方式',
							key: 'repayWay',
							align: 'center'
						},
						{
							title: '进件时间',
							key: 'appDate',
							align: 'center',
							render:(h,params)=>{
								return h('span',params.row.appDate ? new Date(params.row.appDate).Format("MM-dd hh:mm"):'-')
							}
						},
						{
							title: '授信通过日期',
							key: 'auditingTime',
							align: 'center'
						}
					],
					totalPage: 10,
					list:[]
				},
				baseInfo:{},
				userInfo:{},
				disabled:false
			}
		},
		components: {
			searchBox
		},
		created: function () {
			this.loadList();
			this.baseInfo = JSON.parse(sessionStorage.getItem('baseInfo'));
			this.userInfo = JSON.parse(sessionStorage.getItem('userInfo'));
		},
		methods: {
			pageChange: function (val) {
				let _this = this;
				_this.searchParams.currentPage=val;
				_this.loadList();
			},
			pageSizeChange: function (val) {
				let _this = this;
				_this.searchParams.pageSize=val;
				_this.loadList();
			},
			loadList: function () {
				let _this = this;
				_this.$Message.config({
					top: 200,
					duration: 2
				});
				clearTimeout(timer);
				_this.$Message.destroy();
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '数据请求中...',
						duration: 0
					});
				}, 500);
				_this.$http.post('statisticalForm/excelList',_this.searchParams)
					.then(function (response) {
						let res = response.data;
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.refreshFlag=false;
						if(res.code == 1){
							_this.$Message.success(res.msg);
							_this.account.list=res.data.list;
							_this.account.totalPage=res.data.count;
							_this.searchParams.currentPage=res.data.currentPage;
							_this.searchParams.pageSize=res.data.pageSize;
						}else {
							_this.$Message.warning(res.msg);
						}

					})
					.catch(function (error) {
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.refreshFlag=false;
						_this.$Message.error({
							content: error
						})
					})
			},
			search:function (params) {
				params.loanVoucher=params.channelCode;
				_.assign(this.searchParams,params);
				this.loadList()
			},
			downLoadExcel:function () {//下载Excel
				let _this=this;
				_this.disabled=true;
				let form=document.createElement('form');
				let cUserIdInput=document.createElement('input');
				let loanVoucherInput=document.createElement('input');
				let startTimeInput=document.createElement('input');
				let endTimeInput=document.createElement('input');
				form.method='post';
				form.action=`${_this.baseInfo.baseUrl}/statisticalForm/downExcel`;
				form.style.display='none';
				cUserIdInput.name='cUserId';
				cUserIdInput.value=_this.userInfo.userId;
				loanVoucherInput.name='loanVoucher';
				loanVoucherInput.value=_this.searchParams.loanVoucher;
				startTimeInput.name='startTime';
				startTimeInput.value=_this.searchParams.startTime;
				endTimeInput.name='endTime';
				endTimeInput.value=_this.searchParams.endTime;
				form.appendChild(cUserIdInput);
				form.appendChild(loanVoucherInput);
				form.appendChild(startTimeInput);
				form.appendChild(endTimeInput);
				document.body.appendChild(form);
				form.submit();
				setTimeout(function () {
					document.body.removeChild(form);
					_this.disabled=false;
				},10000)
			},
			refresh: function () {
				this.refreshFlag=true;
				this.loadList();
			}
		}
	}
</script>
<style>
	.tab {
		padding-top: 15px;
	}
	.tab-item {
		padding-bottom: 100px;
	}
	.ivu-tabs-tab {
		font-size: 16px;
	}
	.layout-content .extra-btn {
		color: #20abff;
		font-size: 14px;
	}
	.wrap {
		padding-bottom: 15px;
	}
	.chart-box {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-direction: row;
		flex-flow: wrap;
		width: 100%;
	}
	.table-item:nth-child(1) {
		margin-right: 10px;
	}
	.table-item .ivu-table {
		width: auto;
	}
	.chart-item {
		width: 100%;
		height: 500px;
		min-width: 600px;
	}
	.tableHeader{
		height: 30px;
		width: 100%;
		display: flex;
		flex-direction: row;
		justify-content: flex-end;
		align-items: center;
	}
	.export{
		float: right;
	}
	.page {
		padding-top: 50px;
		display: flex;
		justify-content: flex-end;
		flex-direction: row;
		padding-bottom: 50px;
	}
</style>



