<template>
	<div class="search-box" @keyup.enter="searchHistory">
		<div class="search-item"  v-if="itemList.indexOf('channelCode') != -1">
			<div class="item-title color-red">放款渠道</div>
			<Select size="large" v-model="searchParams.channelCode" placeholder="请选择放款渠道"
					style="width: 180px">
				<Option :value="-1">全部</Option>
				<Option :value="1804">新网银行</Option>
				<Option :value="1802">外贸信托</Option>
			</Select>
		</div>
		<div class="search-item" v-if="itemList.indexOf('city') != -1">
			<i-input size="large" v-model="searchParams.city" style="width: 260px"
					 placeholder="请输入业务城市">
				<span slot="prepend" class="input-title">业务城市</span>
			</i-input>
		</div>
		<div class="search-item" v-if="itemList.indexOf('aBaseName') != -1">
			<i-input size="large" v-model="searchParams.aBaseName" style="width: 260px"
				   placeholder="请输入客户姓名">
			<span slot="prepend" class="input-title">客户姓名</span>
			</i-input>
		</div>
		<div class="search-item"  v-if="itemList.indexOf('outReason') != -1">
			<div class="item-title color-red">所在环节</div>
			<Select size="large" v-model="searchParams.outReason" placeholder="请选择订单所在环节"
					style="width: 180px">
				<Option :value="-1">全部</Option>
				<Option :value="1">授信环节</Option>
				<Option :value="2">放款环节</Option>
			</Select>
		</div>
		<div class="search-item" v-if="itemList.indexOf('loanPurpose') != -1">
			<div class="item-title color-red">贷款用途</div>
			<Select size="large" v-model="searchParams.loanPurpose" placeholder="请选择贷款用途"
					style="width: 180px">
				<Option value="全部">全部</Option>
				<Option value="综合消费">综合消费</Option>
				<Option value="生产经营">生产经营</Option>
			</Select>
		</div>
		<div class="search-item" v-if="itemList.indexOf('status') != -1">
			<div class="item-title color-red">审核状态</div>
			<Select size="large" v-model="searchParams.status" placeholder="请选择审核结果"
					style="width: 180px">
				<Option :value="4">全部</Option>
				<Option :value="1" v-if="stage == 'complete'">完成放款</Option>
				<Option :value="1" v-else>通过</Option>
				<Option :value="2">未通过</Option>
			</Select>
		</div>
		<div class="search-item" v-if="itemList.indexOf('dateRange') != -1">
			<div class="item-title color-green">日期范围</div>
			<Date-picker size="large" type="daterange" :editable="false" placement="bottom-end"
						 format="yyyy-MM-dd" placeholder="选择时间范围--年/月/日" style="width:352px"
						 v-model="dateRange" @on-change="dateChange"
			></Date-picker>
		</div>
		<div class="search-item">
			<i-button type="primary" size="large" class="search" :disabled="refreshFlag" @click="searchHistory">查询</i-button>
			<i-button type="success" size="large" class="search" @click="clearAll">清空</i-button>
		</div>
	</div>
</template>

<script>
export default {
    data () {
        return {
			dateRange:[],
			searchParams:{
				aBaseName: '',
				channelCode:-1,
				city:'',
				eProduct: '',
				loanPurpose:'全部',
				status: 4,
				startTime: '',
				endTime: '',
				outReason:-1//所在环节
			}
        }
    },
	props:{
		itemList: {
			type: Array,
			default: function () {
				return ['aBaseName','status','dateRange','channelCode','loanPurpose','city','tache']
			}
		},
		refreshFlag:{
			type: Boolean
		},
		stage:{
			type: String,
			default: function () {
				return 'ing'
			}
		}
	},
	methods:{
		dateChange: function (range) {
			let _this = this;
			if (_.includes(range, undefined)) {
				_this.searchParams.startTime = '';
				_this.searchParams.endTime = '';
			} else {
				_this.searchParams.startTime = range[0];
				_this.searchParams.endTime = range[1];
			}
		},
		searchHistory: function () {
			let _this = this;
			let searchParams=_this.searchParams;
			let params={};
			//搜索,判断需要输出的数据
			let list=_this.itemList;
			let len=_this.itemList.length;
			for(let i=0;i<len;i++){
				if(list[i] == 'dateRange'){
					params.startTime=searchParams.startTime;
					params.endTime=searchParams.endTime;
				}else {
					params[list[i]]=searchParams[list[i]];
				}
			}
			//清空页码
			_this.searchParams.currentPage = 1;
			_this.searchParams.pageSize = 10;
			_this.$emit('search',params)
		},
		clearAll: function () {//清空搜索信息
			this.searchParams.endTime = '';
			this.searchParams.startTime = '';
			this.searchParams.status = 4;
			this.searchParams.eProduct = '';
			this.searchParams.aBaseName = '';
			this.dateRange = [];
		}
	}
}
</script>

<style>
	.search-box {
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-items: center;
		padding-top: 14px;
		padding-bottom: 15px;
		flex-wrap: wrap;
	}

	.search-item {
		flex: 1;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		padding-bottom: 15px;
		margin-right: 10px;
	}

	.item-title {
		width: 80px;
		height: 36px;
		font-size: 14px;
		line-height: 36px;
		text-align: center;
		color: #fff;
		border-top-left-radius: 4px;
		border-bottom-left-radius: 4px;
	}

	.color-yellow {
		background-color: #f0b320;
	}

	.color-red {
		background-color: #ff7043;
	}

	.color-green {
		background-color: #28b779;
	}

	.search {
		width: 100px;
		margin-right: 10px;
	}
	.ivu-select-selection {
		border-top-left-radius: 0px;
		border-bottom-left-radius: 0px;
	}
	.search-item .ivu-input {
		border-top-left-radius: 0px;
		border-bottom-left-radius: 0px;
	}
	.search-box .search-item .ivu-input-group-prepend {
		background-color: #20abff;
		border-color: #20abff;
		width: 80px;
	}

	.input-title {
		color: #fff;
		font-size: 14px;
	}
</style>



